import {DELETE_EMPLOYE_FAIL, DELETE_EMPLOYE_REQUEST, DELETE_EMPLOYE_SUCESS, EMPLOYE_DETAILS_FAIL, EMPLOYE_DETAILS_REQUEST, EMPLOYE_DETAILS_SUCCESS, EMPLOYE_ENTER_FAIL, EMPLOYE_ENTER_REQUEST, EMPLOYE_ENTER_SUCESS, EMPLOYE_FAIL, EMPLOYE_REQUEST, EMPLOYE_SUCESS, UPDATE_EMPLOYE_FAIL, UPDATE_EMPLOYE_REQUEST, UPDATE_EMPLOYE_SUCCESS} from '../constants/employe.js'

export const listEmployesReducer=(state={employ:[]},action)=>{
    switch(action.type){
        case EMPLOYE_REQUEST:
            return{
                loading:true
            }
        case EMPLOYE_SUCESS:
            return{
                loading:false,
                employ:action.payload.data
            }
        case EMPLOYE_FAIL:
            return{
                loading:false,
                error:action.payload
            }
        default:
            return state;
    }

}
const initalState={
    users:localStorage.getItem('employe')?JSON.parse(localStorage.getItem('employe')):[],
    loading:true
}
export const employeEnterReducer=(state=initalState,action)=>{
    console.log(action.payload)
    switch(action.type){
        case EMPLOYE_ENTER_REQUEST:
            return{
                ...state,
                loading:true,
            
            }
        case EMPLOYE_ENTER_SUCESS:
            return{
                ...state,
                loading:false,
                users:[...initalState.users,action.payload.data]
              
            }
        case EMPLOYE_ENTER_FAIL:
            return{
                loading:false,
                error:action.payload
            }
        default:
            return state;
    }

}
export const detailsEmployeReducer=(state={user:{},loading:true},action)=>{
    
    switch(action.type){
        case EMPLOYE_DETAILS_REQUEST:
            return{
            
                loading:true,
                user:{}
               
            }
        case EMPLOYE_DETAILS_SUCCESS:
            return{
              
                loading:false,
                user:action.payload
              
            }
        case EMPLOYE_DETAILS_FAIL:
            return{
                loading:false,
                error:action.payload
            }
        default:
            return state;
    }

}
export const updateEmployeReducer=(state={},action)=>{
    switch(action.type){
        case UPDATE_EMPLOYE_REQUEST:
            return {
                loading:true

        }
        case UPDATE_EMPLOYE_SUCCESS:
            return{
                loading:false,
                updateUser:action.payload
            }
        case UPDATE_EMPLOYE_FAIL:
            return{
                loading:false,
                error:action.payload
            }
        default:
        return state
    }

}
export const deleteEmployeReducer=(state={loading:true},action)=>{
    switch(action.type){
        case DELETE_EMPLOYE_REQUEST:
            return{
                loading:true,
                user:{}
            }
        case DELETE_EMPLOYE_SUCESS:
            return{
                loading:false,
                user:action.payload
            }
        case DELETE_EMPLOYE_FAIL:
            return{
                loading:false,
                error:action.payload
            }
        default:
            return state;
    }

}

